/*reset start*/
* {
    margin: 0;
    padding: 0;
}
html {
    width: 100%;
}
body {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    width: 100%;
    max-width: 640px;
    min-width: 320px;
    height: auto;
    margin: 0 auto;
    font-size: 14px;
    font-family: Regular;
    overflow-x: hidden;
    overflow-y: auto;
}
input,
select {
    outline: none;
    border: none;
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
}
h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: normal;
}
a {
    text-decoration: none;
    color: #444;
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
    -webkit-user-select: none;
    -moz-user-focus: none;
    -moz-user-select: none;
}
a:visited {
    border: none;
    outline: none;
}
img {
    max-width: 100%;
    max-height: 100%;
}
input[type="text"],
input[type="password"] {
    border: none;
}
ul,
ol,
li {
    list-style: none;
}
i {
    font-style: normal;
}
img {
    vertical-align: middle;
}

/*Common Tools Class -> start*/
.clearfix:before,
.clearfix:after {
    /*清除浮动*/
    content: "";
    display: block;
    clear: both;
    visibility: hidden;
    line-height: 0;
    height: 0;
    font-size: 0;
}
